import { BaseConnectorConfig } from '@xip-online-data/types';

export interface ApiConfig {
  name: string;
  url: string;
  method: string;
  interval: number;
  body?: string;
  batchSize?: number;
  offsetFilePrefix?: string;
  format?: string;
  listField?: string;
  detailApiUrl?: string;
  detailKeyFieldSelector?: string;
  keyField?: string;
  incrementalField?: string;
  type?: 'metric' | 'document';
  metadata?: {
    [key: string]: string;
  };
}

export function isApiConfigType(obj: ApiConfig): obj is ApiConfig {
  return (
    obj !== undefined &&
    obj !== null &&
    obj.url !== undefined &&
    obj.name !== undefined &&
    typeof obj.name === 'string' &&
    obj.interval !== undefined &&
    typeof obj.interval === 'number' &&
    !isNaN(obj.interval)
  );
}

export interface ApiResponseData {
  data: string;
  status: number;
  statusText: string;
  headers: { [key: string]: string | Array<string> | undefined };
  config: {};
}

export interface ApiSourceConfig extends BaseConnectorConfig {
  tokenUrl?: string;
  clientId?: string;
  clientSecret?: string;
  authorization?: string;
  sessionUrl?: string;
  sessionUsername?: string;
  sessionPassword?: string;
  apiKey?: string;
  forceTls12?: boolean;
  apiCalls: Array<ApiConfig>;
}

export function isApiSourceConfigType(
  obj: ApiSourceConfig,
): obj is ApiSourceConfig {
  return (
    Array.isArray(obj.apiCalls) &&
    obj.apiCalls.every((q) => isApiConfigType(q)) &&
    Array.from(new Set(obj.apiCalls.map((q) => q.name))).length ===
      obj.apiCalls.length
  );
}
